<template>
    <div class="co-container">
        <div class="emotion chart">

        </div>
    </div>
</template>

<script>
require("echarts-liquidfill")
export default {
    data () {
        return {
            myChart:null,
            allData:null,//请求的数据
            commentOther:'',
            commentNeg:'',
            porate:""
        }
    },
    mounted () {
        this.commentOther = this.$store.state.commentother
        this.commentNeg =  this.$store.state.commentneg
        this.porate = ((this.commentOther/(this.commentOther+ this.commentNeg))*100).toFixed(2);
        this.drawLine () 
        this.getData()
        window.addEventListener('resize',this.resizeChart)
    },
    destroyed () {
        window.removeEventListener('resize',this.resizeChart)
    },
    methods : {
        drawLine () {
            this.myChart = this.$echarts.init(document.querySelector('.emotion'))
            let value = this.porate ;
            let data = value/100;
            const initoption = {
                series: [{
                        type: 'liquidFill',
                        radius: '100%',
                        center: ['50%', '50%'],
                        data: [data, data, data], // data个数代表波浪数
                        backgroundStyle: {
                            borderWidth: 1,
                            color: 'transparent'
                        },
                        label: {
                            normal: {
                                formatter: value + '%',
                                textStyle: {
                                    fontSize: 25
                                }
                            }
                        },
                        
                        outline: {
                            show: false,
                        }
                    },
                    {
                        "type": "pie",
                        "center": ["50%", "50%"],
                        "radius": ["80%", "82%"],
                        "hoverAnimation": false,
                        "data": [{
                                "name": "",
                                "value": 500,
                                labelLine: {
                                    show: false
                                },
                                itemStyle: {
                                    color: '#5886f0'
                                },
                                emphasis: {
                                    labelLine: {
                                        show: false
                                    },
                                    itemStyle: {
                                        color: '#5886f0'
                                    },
                                }
                            },
                            { //画中间的图标
                                "name": "",
                                "value": 4,
                                labelLine: {
                                    show: false
                                },
                                itemStyle: {
                                    color: '#ffffff',
                                    "normal": {
                                        "color": "#5886f0",
                                        "borderColor": "#5886f0",
                                        "borderWidth": 20,
                                        // borderRadius: '100%'
                                    },
                                },
                                label: {

                                    borderRadius: '100%'
                                },
                                emphasis: {
                                    labelLine: {
                                        show: false
                                    },
                                    itemStyle: {
                                        color: '#5886f0'
                                    },
                                }

                            },
                            { // 解决圆点过大后续部分显示明显的问题
                                "name": "",
                                "value": 4,
                                labelLine: {
                                    show: false
                                },
                                itemStyle: {
                                    color: '#5886f0'
                                },
                                emphasis: {
                                    labelLine: {
                                        show: false
                                    },
                                    itemStyle: {
                                        color: '#5886f0'
                                    },
                                }
                            },
                            { //画剩余的刻度圆环
                                "name": "",
                                "value": 88,
                                itemStyle: {
                                    color: '#050038'
                                },
                                "label": {
                                    show: false
                                },
                                labelLine: {
                                    show: false
                                },
                                emphasis: {
                                    labelLine: {
                                        show: false
                                    },
                                    itemStyle: {
                                        color: 'rgba(255,255,255,0)'
                                    },
                                }
                            }
                        ]
                    }

                ]
               

                

            }
            this.myChart.setOption(initoption)
        },
        async getData () {
            // await this.$http.get()
            //this.allData = {}
            this.updateChart()
        },
        updateChart () {
            //处理数据
            const dataOption = {}
            this.myChart.setOption(dataOption)
        },
        resizeChart () {
            this.myChart ? this.myChart.resize() : ''
        }
    },
    
    
}
</script>

<style lang= "less">
.co-container{
    width:100%;
    height: 100%;
    .emotion{
        width:100%;
        height: 100%;
        margin : 0 auto;
    }
}
</style>