<template>
    <div class="com-container">
       <!-- <div v-model="gender"></div> -->
        <div class="sex chart">
        </div>
        <div class="male " style="display:none">{{mprop}}%</div>
        <div class="female" style="display:none">{{feprop}}%</div>
    </div>
</template>

<script>
export default {
  
    data () {
        return {
            myChart:null,
            allData:null,//请求的数据
            male:null,
            female:null,
            allNum:null,
            feprop:'',
            mprop:''
        }
    },
   
    mounted () {
        this.drawLine () 
        this.female = parseFloat(localStorage.getItem('f'));
        this.male = parseFloat(localStorage.getItem('m'));
        this.allNum = this.male + this.female;
        this.feprop = (100*this.female / this.allNum ).toFixed(2)
        this.mprop = (100*this.male / this.allNum ).toFixed(2)
        this.getData()
        
        window.addEventListener('resize',this.resizeChart)
    },
     updated(){
           this.drawLine() 
        },
    destroyed () {
        window.removeEventListener('resize',this.resizeChart)
    },
    methods : {
        drawLine () {
            this.myChart = this.$echarts.init(document.querySelector('.sex'))
            const res = [
                {
                    "label": "男",
                    "value": this.male
                },
                {
                    "label": "女",
                    "value": this.female
                }];

const border =  'data:image/png;base64,\
    \
    y8U45QPULzIlh+YRebyGv6icyjX0s+NwkQBY+8GhqoxVBw99YESGHq\
    RVco6ve/m2uEgNDqfi2MFfoumM75BBvAxVKwB4lT3lZLotfk/ew6u4IM3s2a/chcb5vnCs\
    B09tVG+X9wEALYW78mgZLoWlW6sfiO+TospODfddunq2sKa4u1s5/xAr551gZf2zaMtYWfcC2q\
    \
    eo3e8yqzdowX7knDuY925Xv9BUW5zxTsRNCkCK5Pwpes6Jf3Os4zlBIRJMfc2or1HjX3D7QJlXpFOQzWKOAIAUnkdq\
    ';
            const man ='data:image/png;base64,';
            const woman =
                'data:image/png;base64,';
            const color = ['#00FFFF', 'rgba(196,36,255,1)'];
            const data = [];
            for (let i = 0; i < res.length; i++) {
                data.push({
                    value: res[i].value,
                    name: res[i].label,
                    itemStyle: {
                        normal: {
                            borderWidth: 5,
                            shadowBlur: 20,
                            borderColor: color[i],
                            shadowColor: color[i]
                        }
                    }
                }, {
                    value: res[0].value / 10,
                    name: '',
                    itemStyle: {
                        normal: {
                            label: {
                                show: false
                            },
                            labelLine: {
                                show: false
                            },
                            color: 'rgba(0, 0, 0, 0)',
                            borderColor: 'rgba(0, 0, 0, 0)',
                            borderWidth: 0
                        }
                    }
                });
            }
            const initoption = {
                color,
                graphic: {
                    elements: [{
                            type: 'image',
                            z: 3,
                            style: {
                                image: border,
                                width: 100,
                                height: 100
                            },
                            left: 'center',
                            top: 'center',
                            position: [33, 33]
                        },
                        {
                            type: 'image',
                            z: 3,
                            style: {
                                image: man,
                                width: 29,
                                height: 40
                            },
                            left: '36%',
                            top: 'center'
                        },
                        {
                            type: 'image',
                            z: 3,
                            style: {
                                image: woman,
                                width: 29,
                                height: 40
                            },
                            right: '36%',
                            top: 'center'
                        }
                    ]
                },
                tooltip: {
                    show: false
                },
                toolbox: {
                    show: false
                },
                series: [{
                    name: '',
                    type: 'pie',
                    clockWise: false,
                    radius: [58, 60],
                    hoverAnimation: false,
                    itemStyle: {
                        normal: {
                            label: {
                                show: true,
                                fontSize: 16,
                                formatter(params) {
                                    return params.name ?
                                        params.name + '\n' + params.value + '人' :
                                        '';
                                }
                            },
                            labelLine: {
                                width: 4,
                                length: 30,
                                length2: 50,
                                show: true,
                                color: '#00ffff'
                            }
                        }
                    },
                    data
                }]
            }
            this.myChart.setOption(initoption)
        },
        getData () {
          
            this.updateChart()
        },
        updateChart () {
            //处理数据
            const dataOption = {}
            this.myChart.setOption(dataOption)
        },
        resizeChart () {
            this.myChart ? this.myChart.resize() : ''
        }
    }
    
}
</script>

<style lang= "less" scoped>
h2{
    font-size:20px;
    line-height: 1;
    /* //font-weight:lighter; */
}
h3{
    padding: 5px;
    color:#E4E4E4;
    font-size: 15px;
    line-height: 1;
}
.sex{
    width: 80%;
    height: 80%;
}
.male{
    color:#fff;
    float: left;
    margin-left: 70px;
}
.female{
    color:#fff;
    float: left;
    margin-left: 280px;
    margin-top: -25px;
}
</style>