<template>
    <div class="com-container">
        
        <div class="trend chart">

        </div>
    </div>
</template>

<script>
export default {
    props: {
        trendReport:{
            type: Array,
            default: () => []
        }
    },
    data () {
        return {
            myChart:null,
            oldNew:[],
            converData:[]
        }
    },
    mounted () {
        this.oldNew = this.$store.state.history_weibo
        // console.log('取指数',this.converData);
        this.drawLine () 
        this.updateChart()
        window.addEventListener('resize',this.resizeChart)
    },
    destroyed () {
        window.removeEventListener('resize',this.resizeChart)
    },
    methods : {
        drawLine () {
            this.myChart = this.$echarts.init(document.querySelector('.trend'))
            const initoption = {
                
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        lineStyle: {
                            color: {
                                type: 'linear',
                                x: 0,
                                y: 0,
                                x2: 0,
                                y2: 1,
                                colorStops: [{
                                    offset: 0,
                                    color: 'rgba(0, 255, 233,0)'
                                }, {
                                    offset: 0.5,
                                    color: 'rgba(255, 255, 255,1)',
                                }, {
                                    offset: 1,
                                    color: 'rgba(0, 255, 233,0)'
                                }],
                                global: false
                            }
                        },
                    },
                },
                grid: {
                    top: '23%',
                    left: '5%',
                    right: '5%',
                    bottom: '1%',
                    containLabel: true,
                },
                xAxis: [{
                    type: 'category',
                    axisLine: {
                        show: true
                    },
                    splitArea: {
                        // show: true,
                        color: '#f00',
                        lineStyle: {
                            color: '#f00'
                        },
                    },
                    axisLabel: {
                        color: '#A9AFC7'
                    },
                    splitLine: {
                        // show: falses
                        show: true,
                        lineStyle: {
                        color: ['#fff'],
                        opacity: 0.06
                        }
                    },
                    boundaryGap: false,
                    data: ["-5","-4","-3","-2","-1","预测"],

                }],

                yAxis: [{
                    type: 'value',
                    min: 0,
                    // max: 140,
                    splitNumber: 4,
                    splitLine: {
                        show: true,
                        lineStyle: {
                        color: ['#fff'],
                        opacity: 0.06
                        }
                    },
                    axisLine: {
                        show: true,
                    },
                    
                    axisLabel: {
                        show: true,
                        margin: 20,
                        textStyle: {
                        fontSize: 10,
                        color: '#A9AFC7'
                        }
                    },
                    axisTick: {
                        show: true,
                    },
                }],
               

            }
            this.myChart.setOption(initoption)
        },
        
        updateChart () {
            //处理数据
            let hotValue =[];
            this.oldNew.map(item => {
                hotValue.push(item.hot_value)
            })  
            this.converData = (hotValue.slice(0,6).reverse())
            const dataOption = {
                 series: [{
                        name: '历史新闻指数',
                        type: 'line',
                        smooth: false, //是否平滑
                        showAllSymbol: true,
                        // symbol: 'image://./static/images/guang-circle.png',
                        symbol: 'circle',
                        symbolSize: 10,
                        lineStyle: {
                            normal: {
                                color: "#2CABE3",
                            },
                        },
                        label: {
                            show: true,
                            position: 'top',
                            textStyle: {
                                color: '#2CABE3',
                            }
                        },
                        itemStyle: {
                            color: "#fff",
                            borderColor: "#2CABE3",
                            borderWidth: 2,
                        },
                        tooltip: {
                            show: false
                        },
                        	areaStyle: { //区域填充样式
                                normal: {
                                //线性渐变，前4个参数分别是x0,y0,x2,y2(范围0~1);相当于图形包围盒中的百分比。如果最后一个参数是‘true’，则该四个值是绝对像素位置。
                                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                    { offset: 0,  color: 'rgba(61,234,255, 0.9)'}, 
                                    { offset: 1,  color: 'rgba(61,234,255, 0)'}
                                ], false),

                                shadowColor: 'rgba(53,142,215, 0.9)', //阴影颜色
                                shadowBlur: 20 //shadowBlur设图形阴影的模糊大小。配合shadowColor,shadowOffsetX/Y, 设置图形的阴影效果。
                            }
                        },
                        
                        data:this.converData
                    }
                ]
            }
            this.myChart.setOption(dataOption)
        },
        resizeChart () {
            this.myChart ? this.myChart.resize() : ''
        }
    }
    
}
</script>

<style lang="less">
.com-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    
    .title{
        width: 100%;
       
        h2{
            color: #fff;
            font-size:20px;
            line-height: 1;
        }
    }
    .trend{
    width: 100%;
    height: 80%;
    bottom:5%
    }
}


</style>