<template>
<div>
<div id="printMe" style="padding:20px">
    <div class="report_box">
    <div class="head_bkg"></div>
                  <div class="report_content">
                      <div class="report_analyse">
                          <div>
                              <div class="title">新闻原文</div>
                              <p style="color:#fff" v-model="text">{{text}}</p>
                          </div>
                          <div class="personal_msg">
                            <div class="title">博主个人信息</div>
                              <div class="msg_content" v-model="personalData">
                                 <img :src="personalData.img" alt="">
                                 <div class="content-text">
                                   <div>昵称：{{personalData.nickName}}</div>
                                  <div>性别：{{personalData.gender}}</div>
                                   <div>ID：{{personalData.ID}}</div>
                                   <div>博文ID：{{personalData.TextID}}</div>
                                 </div>
                              </div>
                          </div>
                          <div class="history_box">
                              <div class="history_newsIndex">
                                  <div class="title">博主历史新闻热度</div>
                                  <div class="e-chart">
                                    <newsTrend/>
                                  </div>
                              </div>

                              <div class="history_news">
                                  <div class="title">博主历史新闻</div>
                                  <div class="news_conent">
                                    <el-table :data="tableData" style="width: 100%">
                                      <el-table-column label="新闻内容" width="400" prop="raw_text"  >
                                         
                                      </el-table-column>
                                       <el-table-column label="发布时间" width="120" prop="created_at"  >
                                        
                                      </el-table-column>
                                      
                                    </el-table>
                                    

                                  </div>                                
                              </div>
                          </div>
                          <div class="commenter">
                            <div class="emotion-proportion">
                              <div class="title">评论情感占比</div>
                              <div class="e-chart">
                                <div class="item">
                                  <div class="pie">
                                    <emotionPie/>
                                  </div>
                                  <div class="po">正面文本</div>
                                  
                                </div>
                                <div class="item">
                                  <div class="pie">
                                    <emotionNe/>
                                  </div>
                                  <div class="po">负面文本</div>

                                </div>
                                
                              </div>
                            </div>
                            <div class="commenter_analyse">
                              <div class="title">评论者性别分析</div>
                              <div class="e-chart">
                                <sexProp/>
                              </div>
                            </div>
                            
                          </div>
                          <div class="hot_comments">
                              <div class="title">热点评论</div>
                             <div class="hot_comments_content">
                               <div v-for="(item,index) in commentData" :key="index">
                                 <img :src="item.comment_avatar" alt="">
                                 <div class="content-text">
                                   <div v-model="item.comment_id">{{item.comment_id}}</div>
                                   <div v-model="item.content">{{item.comment_content}}</div>
      
                                 </div>
                               </div>
                             </div>
                          </div>
                          <div class="word_cloud">
                              <div class="title">热点评论词云</div>
                             
                              <div class="e-chart">
                                <wordCloud/>
                                  
                              </div>
                          </div>
                      </div>
                  </div>       
  </div>
</div>
<el-button type="warning" class="do_report" ><a :href="'http://www.yanzx.top/api/news/exportExcel/?weibo_id=' + url">导出EXCLE</a></el-button>
</div>


</template>

<script>
import trend from '../analyse/analyse_echart/trend'
import emotionNe from "../analyse/analyse_echart/emotion-ne"
import emotion from "../analyse/analyse_echart/emotion";
import sex from "../analyse/analyse_echart/sex";
import keyword from '../analyse/analyse_echart/keyword'


export default {

   data() {
      return {
        url:  this.$store.state.weibo_id,
        // localStorage.getItem('weibo_id'),
        text: localStorage.getItem('raw_text'),
        htmlTitle:"PDF名称",
        nowTime:"2020-1-1",
         printObj: {
              id: "printMe",
              popTitle: 'good print',
              extraCss: 'https://www.google.com,https://www.google.com',
              extraHead: '<meta http-equiv="Content-Language"content="zh-cn"/>'
            },

        personalData:{
          img:localStorage.getItem('author_avatar'),
          nickName: localStorage.getItem('authorId'),
          gender:localStorage.getItem('author_gender') == 'f' ? "男" : "女",
          ID:localStorage.getItem('author_id_id'),
          TextID : localStorage.getItem('weibo_id')
        },
       history_weibo:[],
        tableData:[],
        commentData:this.$store.state.hot_comments
      }
    },
    mounted () {
     
      this.history_weibo = this.$store.state.history_weibo;
      let historyData = this.history_weibo.map(item => {
        return {
          raw_text:(item.raw_text).substring(0,30) +`...`,
          created_at:item.created_at
        }
      })
      this.tableData = historyData.slice(0,5)
      
    },
 
    methods: {
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
      

    },
   
   components: {
     newsTrend:trend,
      emotionPie: emotion,
      emotionNe: emotionNe,
      sexProp: sex,
      wordCloud:keyword
   }

}
</script>

<style lang="less">

.report_box{
  .title{
    color: #10E7F8;
    font-size: 25px;
    font-weight: blod;
    margin: 50px 0px 20px 0px;
  }
  .report_content{
    margin-top: 100px;
    color: #5e5f61;
    .report_analyse{
      margin:0px 30px;
      .history_box{
            @import '../../../../assets/less/teble.less';
            width: 100%;
            display: flex;
            flex-direction: row;
            .history_newsIndex{
              width: 50%;
              .e-chart{
                width: 500px;
                height: 300px;
              }
            }
            .history_news{
              width: 50%;
            }
      }
    .personal_msg{
      img{
        width: 100px;
        height: 100px;
        border-radius: 50%;
        margin: 15px 20px 0px 20px;
      }
      .msg_content{
        display: flex;
        flex-direction: row;
        .content-text{
          padding-top:25px;
          margin-left: 30px;
          color: white;
          font-size: 18px;
          font-weight: bold;
        }
      }
    }
    .commenter{
      position: relative;
      .emotion-proportion{
        .e-chart{
          width: 500px;
          height: 200px;
          display: flex;
          flex-direction: row;
          .item{
            width:50%;
            height: 80%;
            margin-right:10px;
            display: flex;
            flex-direction: column;
            .pie{
              width:100%;
              height: 80%;
            }
            .po{
              color:#fff;
              margin-top:10px;
              height: 20%;
              display: flex;
              justify-content: center;
            }
          }
        }
      }
      .commenter_analyse{
          position: absolute;
        top: -45px;
        right: 100px;
        margin-left: -200px;
          .e-chart{
          width: 500px;
          height: 200px;
          margin-right: 5px;
          margin: 0 auto;
        }
      }
    }
    .hot_comments{
      img{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin: 15px 20px 0px 20px;
      }
      .hot_comments_content{
        display: flex;
        flex-wrap: wrap 
      }
      .hot_comments_content>div{
        background: url('../../../../assets/img/manage/border2.png');
        background-size: 100% 100%;
        position: relative;
        display: flex;
        margin: 20px;
        width: 400px;
        height: 80px;
        .content-text div:nth-child(1){
          margin-top: 15px;
          font-size: 12px;
          font-weight: bold;
          color: #10E7F8;
        }
          .content-text div:nth-child(2){
          margin-top: 3px;
          font-size: 10px;
          color: #10E7F8;
        }
        .content-text div:nth-child(3){
          position: absolute;
          right: 5px;
          bottom: 5px;
          font-size: 5px;
          color: #10E7F8;
        }
      }
    }
    .word_cloud{
        .e-chart{
          width: 80%;
          height: 300px;
          margin: 50px 0px;
          margin-left: 50%;
          transform: translate(-50%,0%);
      }
    }
    }
  
  } 
}

  .do_report{
     a{
      text-decoration: none;
      color: #0CDCF1;
      }
     position: fixed;
     right: 40px;
     top: 50px;   
   }
    .el-button--warning{
      font-size: 14px;
      font-weight: bold;
      background-color: transparent;
      border: 2px solid #10E7F8;
    }
    
    .el-button--warning:hover,.el-button--warning:focus{
      color:  #95f1f8;
      font-size: 14px;
      font-weight: bold;
      background-color: #95f2f881;
      border: 2px solid #10E7F8;

    }
    
</style>