<template>
    <div class="com-container">
        <div class="keyword chart">

        </div>
    </div>
  
</template>

<script>
require("echarts-wordcloud");
export default {
    data () {
        return {
            myChart:null,
            allData:null,//请求的数据
            cloudData:{
                value:[],
                image:"data:image/png;base64,"
            },
            cloud:[],
        }
    },
    mounted () {
        this.cloudData.value = this.$store.state.comment_word_cloud
        
        // console.log('传的评论词云数据',this.cloud)
        this.drawLine () 
        // this.getData()
        window.addEventListener('resize',this.resizeChart)
    },
    destroyed () {
        window.removeEventListener('resize',this.resizeChart)
    },
    methods : {
        drawLine () {
            this.myChart = this.$echarts.init(document.querySelector('.keyword'))
            var maskImage = new Image();
            maskImage.src = this.cloudData.image
            this.myChart.setOption({
                 series: [{
                    type: 'wordCloud',
                    gridSize: 1,
                    sizeRange: [12, 55],
                    rotationRange: [-45, 0, 45, 90],

                    size: ['100%', '100%'],
                    // sizeRange: [5, 40],//最小文字——最大文字
                    // textRotation: [0, 45, 90, -45],
                    // rotationRange: [-45, 90],//旋转角度区间
                    // rotationStep: 90,//旋转角度间隔
                    shape: 'circle',
                    // gridSize: 10,//字符间距
                    // gridSize: 6,
                    maskImage:maskImage,
                    textStyle: {
                        normal: {
                            color: function(v) {
                                let color = ['#27D38A', '#FFCA1C', '#5DD1FA', '#F88E25','#47A0FF','#FD6565']
                                let num =Math.floor(Math.random() * (5 + 1));
                                // console.log(num)
                                return color[num];
                            },
                        },
                    },
                    left: 'center',
                    // top: '10',
                    width: '200%',
                    height: '200%',
                    data:this.cloudData.value
                }]
            });
            
            
        },
        
        updateChart () {
            //处理数据
            const dataOption = {}
            this.myChart.setOption(dataOption)
        },
        resizeChart () {
            this.myChart ? this.myChart.resize() : ''
        }
    },

}
</script>

<style>
.keyword{
    width: 100%;
    height: 100%;
}

</style>